.HomeSkills {
    color: black;
    height: auto;
    margin-top: 5em;
}
.mobile-hide {
    display: none;
}
.mobile-unhide {
    display: flex;
}

.HomeSkills h2 {
    font-size: 4em;
    margin-bottom: 0 !important;
}
.HomeSkills hr {
    border: 1px solid rgba(255,255,255,.3);
    width: 70%;
    margin-bottom: 2em;
}
.skillsContainer {
    justify-content: center;
    min-height: 520px;
}
.buttonContainer {
    padding: 50px;
    width: 80%;
    margin: 0 auto;
    justify-content: center;
}
.buttonContainer .btn {
    margin: 5px;
    max-width: 60%;
    min-width: 150px;
    font-size: 24px;
    font-weight: bold;
}

.icon-size {
    width: 50%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: .5em;
    margin: 0 auto;
    justify-content: center;
    height: 100%;
    font-size: 5em;

}
.icon-size > i:hover {
    background: rgba(255,255,255,.5);
    /* padding: 10px; */
}
.icon-size > i > span:hover {
    background: none;
}
i {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding: 10px;
    transition: 1s;
    border-radius: 20px;

}

i span {
    font-family: 'Handlee';
    color: black;
    margin: 1em auto 0.5em auto;
    font-size: .3em;
}

.selected {
    position: relative;
}
.selected:after {
    position: absolute;
    content: '';
    border-bottom: 2px solid #d2d7da;
    width: 80%;
    transform: translateX(-50%);
    bottom: -25px;
    left: 50%;
}

@media (max-width: 900px) {
    .selected:after {
        bottom: -7px;
    }
    .icon-size {
        width: 70%;
        grid-template-columns: repeat(2,1fr);
        gap: .1em;
    }
    .HomeSkills h2 {
        font-size: 3em;
    }
    .buttonContainer {
        padding: 20px;
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .mobile-hide { 
        display: grid !important;
        grid-template-columns: 1fr 1fr;
    } 
    .mobile-unhide {
        display: none;
    }
}