
.HomeExperience {
    margin: 5em;
}

.hidden2 {
    position: relative;
    z-index: -1;
    width: 0px;
    height: 524px;
    max-height: 524px;
    opacity: 0;
    margin-left: -50%;
    padding: 5px 0;
    background: white;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    border-radius: 0 5px 5px 0;
    animation: 2s slide-left;
    transition: width 2s, height 2s, opacity 1s;
}
.unhidden2 {
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: left;
    background: white;
    border-radius: 0 15px 15px 0;
    width: 150px;
    height: 524px;
    max-height: 524px;
    padding: 5px 0;
    opacity: 1;
    animation: 1.5s slide-right;
    transition: width 1s, opacity 3s;
}

@media (max-width: 1300px) {
    .unhidden2 {
        max-height: 431px;
        height: 431px;
        overflow: scroll;
    }
    .hidden2 {
        height: 431px;
        max-height: 431px;
        transition: width 2s, height 0s, opacity 1s;
    }

    .HomeExperience {
        margin: 0px;
    }
}